*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}
.app{
    display:flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}
.grid{
    display: grid;
    grid-template-columns: auto auto auto;
    grid-template-rows: auto auto auto;
    gap:10px;
    background-color:lightgrey;
}